import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {OrdersModule} from './orders.module';

import {LoginComponent, DashboardComponent, UsersComponent, RefundComponent} from '../components';

import {AuthGuard} from '../services';

declare var require: any;

const routes: Routes = [
    { path: 'login' , component: LoginComponent},    
    { path: 'login/:error' , component: LoginComponent},        
    { path: 'dashboard', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\dashboard.module')['DashboardModule']);  }, 'bundle');})},
    { path: 'usersOrders', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\orders.module')['OrdersModule']);  }, 'bundle');})},
    {path: '', redirectTo: '/login', pathMatch: 'full'}    
]

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule{}


